/**
 * @module ext-portfolio-positions-module
 *
 * @description
 * Module containing components and directives used by positions widget.
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBbPortfolioPositionsKey from 'ui-wm-portfolio-positions-ng';
 *
 * export const dependencyKeys = [
 *   uiBbPortfolioPositionsKey,
 * ];
 *
 */
import angular from 'vendor-bb-angular';
import PortfolioRowComponent from './portfolio-row/component';
import AssetClassRowComponenent from './asset-class-row/component';
import RegionRowComponent from './region-row/component';
import PositionsTableDirective from './positions-table/directive';

/**
 * @name default
 * @type {string}
 * @description Angular module name
 */
export default angular
  .module('ui-wm-portfolio-positions-ng', [])
  .directive('uiWmPositionsPositionsTable', PositionsTableDirective)
  .component('uiWmPositionsPortfolioRow', PortfolioRowComponent)
  .component('uiWmPositionsAssetClassRow', AssetClassRowComponenent)
  .component('uiWmPositionsRegionRow', RegionRowComponent)
  .name;



